<template>
  <div id="app" style="width: 100%;height: 100%;background-color: #fbfbfb;">
    <audio ref="mp3" preload="auto" style="display: none" id="mp3">
      <source src="http://localhost/ AP1.668(NotificationMulti___aigei_com.mp3" type="audio/mpeg">
    </audio>
    <h1></h1>
    <h4></h4>
    <el-button type="warning" style="margin-left: 1000px">
      <el-link icon="el-icon-edit" :underline="false"  @click="update"></el-link>
    </el-button>
    <br>
    <br>
    <el-radio-group v-model="selectedAddress" @change="handleAddressChange"
                    v-if="addressList.length>0" class="radio">
      <el-card    v-for="(addr,index) in addressList" :key="index"
                  style="display: flex;width: 1200px;height: 160px"
                  :class="{'active':selectedAddress === index}"
                  @click="selectedAddress=index"
      >
        <el-radio  border style="width: 1150px;height: 120px;" :label="index">
          <div style="margin-left: 60px">{{ userInfo.userName }}
            <span style="padding-left: 250px">{{ userInfo.userTelephone }}</span>
            <span style="padding-left: 250px">{{ addr.realName }}</span>
          </div>
          <div style="margin-left: 60px;margin-top: 35px">
            <span>:<el-tag type="warning" style="margin-left: 20px">{{ addr.addressTag }}</el-tag></span>
            <span style="padding-left: 250px">{{ addr.addressDetail }}</span>
          </div>
        </el-radio>
      </el-card>
    </el-radio-group>
    <el-card v-else style="width: 1200px">
      <el-empty description="" ></el-empty>
    </el-card>
    <div style="margin-left: 1100px">
    <el-button type="warning" @click="handleUpdate"></el-button>
    </div>
    <div>
      <h3></h3>
      <div style="margin-left: 50px">
        <el-radio v-model="radio" label="1" border></el-radio>
        <el-radio v-model="radio" label="2" border></el-radio>
        <el-radio v-model="radio" label="3" border></el-radio>
        <el-radio v-model="radio" label="4" border></el-radio>
        <el-radio v-model="radio" label="5" border>/</el-radio>
        <el-radio v-model="radio" label="6" border></el-radio>
      </div>
       <div style="margin-top: 20px;margin-bottom: 15px"></div>
        <el-input type="textarea" v-model="sendData.orderRemark" style="width: 1200px;height: 100px"></el-input>
      <div style="margin-left: 1050px">
      <el-button type="warning" @click="handon"></el-button>
      <el-button @click="onCancel2"></el-button>
      </div>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="50%"
    >
      <el-form ref="form" :model="addressForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="addressForm.realName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addressForm.addressTag" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="addressForm.addressDetail"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="warning" @click="onSubmit"></el-button>
          <el-button @click="onCancel"></el-button>
        </el-form-item>


      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import axios from "axios";

export default {
  data(){
    return{
      userInfo:[],
      tableData:[],
      addressForm:{
        addressDetail:"",
        realName:"",
        addressTag:"",
        userId:""
      },
      addressList:[],
      selectedAddress:0,
      dialogVisible:false,
      radio:1,
      sendData:{
        orderId:"",
        orderAddress:"",
        userId:0,
        brandId:0,
        orderRemark:"",
        amount:"",
        orderPrice:""
      },
      orderData:{
        orderId:"",
        orderStatus:"",
        orderRemark:""
      }
    }
  },
  mounted() {
    const _this = this
    const brandId = this.$route.query.brandId
    _this.ws = new WebSocket(`ws://localhost/ws/${_this.userId}`)
    _this.ws.onmessage = function (event) {
      _this.$refs.mp3.play().catch(err => {console.log("no")})
      const h = _this.$createElement;

      _this.$notify({
        title: '',
        message: h('i', { style: 'color: teal'}, ``)
      });
    }
    axios({
      method:"get",
      url:`http://localhost/brands/findById?brandId=${brandId}`
    }).then(function (resp){
      _this.tableData = resp.data.data
      _this.originPrice = resp.data.data.brandPrice
    })
    axios.get("http://localhost/users/selectByUserName").then(function (resp){
      _this.userInfo = resp.data
      _this.addressForm.userId = resp.data.userId
      _this.userId = resp.data.userId
    })
    axios.get(`http://localhost/address/findAddress`).then(function (resp){
      _this.addressList=resp.data
    })
  },
  beforeDestroy() {
    if(this.ws){
      this.ws.close();
      this.ws = null
    }
  },
  methods:{
    update(){
      this.dialogVisible = true
    },
    onSubmit(){
      const _this = this
      axios({
        method:"post",
        url:"http://localhost/address/addAddress",
        data:_this.addressForm
      }).then(function (resp){
        if(resp.data.status === 20211){
          _this.$message({
            message: resp.data.message,
            type: 'success',
          })
        }else{
          _this.$message({
            message: resp.data.message,
            type: 'error',
          })
        }
      })
    },
    onCancel(){
      this.dialogVisible = false
    },
    onCancel2() {
      this.$router.push('/order')
    },
    handleAddressChange(index){
      this.sendData.orderAddress=this.addressList[index].addressDetail
      this.sendData.orderId=this.$route.query.orderId
    },
    handleUpdate(index){
      const _this = this
      this.sendData.orderId=this.$route.query.orderId
      console.log(index)
      this.sendData.orderAddress=this.addressList[this.selectedAddress].addressDetail

      axios({
          method:"post",
          url: `http://localhost/orders/updateAddress`,
          data: _this.sendData
      }).then(function (resp){
        _this.$message({
          message: resp.data.message,
          type: 'success',
        })
      })
    },
    handon(){
        const _this = this
      this.orderData.orderId = this.$route.query.orderId
      this.orderData.orderRemark=this.sendData.orderRemark
      axios({
        method:"post",
        url:`http://localhost/orders/updateAddress`,
        data:_this.orderData
      }).then(function (resp){
        if(resp.data.status === 20320){
          _this.$message({
            message: resp.data.message,
            type: 'success',
            onClose: function () {
              _this.$router.push('/order')
            }
          })
        }else{
          _this.$message({
            message: resp.data.message,
            type: 'error',
          })}
      })
    }
  }
}
</script>