<template>
  <div id="app" style="width: 100%;height: 100%;background-color: #fbfbfb;">
    <audio ref="mp3" preload="auto" style="display: none" id="mp3">
      <source src="http://localhost/ AP1.668(NotificationMulti___aigei_com.mp3" type="audio/mpeg">
    </audio>
    <h2></h2>
    <div style="display: flex;margin-left: 100px;line-height: 50px">
      <span></span>
      <el-rate
        v-model="value"
        show-text style="margin-left: 60px;line-height: 60px">
    </el-rate></div>
    <div style="display: flex;margin-left: 100px;line-height: 50px">
    <span>:</span>
    <el-rate
        v-model="value1"
        show-text style="margin-left: 60px;line-height: 60px">
    </el-rate>
    </div>
    <div style="display: flex;margin-left: 100px;line-height: 50px">
      <span>:</span>
      <el-rate
          v-model="value2"
          show-text style="margin-left: 60px;line-height: 60px">
      </el-rate>
    </div>
    <div style="margin-left: 100px;margin-top: 50px">
      <span></span>
      <el-button class="el-icon-picture" type="warning" style="margin-left: 850px"></el-button>
    <el-input
        type="textarea"
        placeholder=""
        v-model="sendData.commentDetail"
        maxlength="100"
        show-word-limit
        style="width: 90%;margin-top: 30px;"
    >
    </el-input>
    </div>
    <div style="display: flex;margin-left: 1120px;margin-top: 30px">
      <router-link to="/order" style="margin-right: 10px">
      <el-button type="info"></el-button>
      </router-link>
    <el-button type="warning" @click="comment"></el-button>
    </div>
  </div>
</template>
<style>

</style>
<script>
import axios from "axios";


export default {
  data() {
    return {
      value:0,
      value1:0,
      value2:0,
      sendData:{
        commentDetail:"",
        brandId:0,
        userId:0,
        rate:0
      },
      orderData:{
        orderId:"",
        orderStatus:""
      }

    }
  },
  mounted() {
    const _this = this
    axios.get("http://localhost/users/selectByUserName").then(function (resp){
      _this.userId = resp.data.userId
      _this.ws = new WebSocket(`ws://localhost/ws/${_this.userId}`)
      _this.ws.onmessage = function (event) {
        _this.$refs.mp3.play().catch(err => {console.log("no")})
        const h = _this.$createElement;
        _this.$notify({
          title: '',
          message: h('i', { style: 'color: teal'}, ``)
        });
      }
    })
  },
  beforeDestroy() {
    if(this.ws){
      this.ws.close();
      this.ws = null
    }
  },
  methods:{
    comment(){
      console.log(this.value)
      const _this = this
      this.sendData.brandId = this.$route.query.brandId
      this.sendData.rate = this.value
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log(_this.sendData)
        axios({
          method: "post",
          url:"http://localhost/comments/addComment",
          data:_this.sendData
        }).then(function (resp){
          if(resp.data.status === 20401){
            _this.orderData.orderId =_this.$route.query.orderId
            _this.orderData.orderStatus = ""
            axios({
              method:"post",
              url:"http://localhost/orders/updateAddress",
              data:_this.orderData
            })
            _this.$message({
              message: resp.data.message,
              type: 'success',
              onClose(){
                _this.$router.push("/order")
              }
            })
          }else {
            _this.$message({
              message: resp.data.message,
              type: 'error',
            })
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    }
  }
}
</script>